@import '~antd/dist/antd.css';

:root {
  --timeline-baseline: 17%;
}

.App {
  padding: 10px;
}

main {
  padding: 15px;
  margin: 0 auto;
  max-width: 750px;
}

/* Elements */

img {
  max-width: 100%;
}

h3 {
  display: inline-block;
}

p {
  font-size: 1rem;
}

code {
  background-color: #F6F6F6;
  color: #E01E5A;
  border: 1px solid #DADADA;
  border-radius: 3px;
  padding: 2px 5px;
}

.icon {
  height: 150px;
  width: 150px;
  border-radius: 10px;
}

.subtitle {
  color: rgba(0, 0, 0, 0.5);
}

/* Antd Timeline */

.ant-timeline.ant-timeline-label .ant-timeline-item-label {
  width: calc(var(--timeline-baseline) - 12px);
}

.ant-timeline.ant-timeline-label .ant-timeline-item-tail {
  left: var(--timeline-baseline);
}

.ant-timeline.ant-timeline-label .ant-timeline-item-head {
  left: var(--timeline-baseline);
}

.ant-timeline.ant-timeline-label .ant-timeline-item-left .ant-timeline-item-content {
  left: calc(var(--timeline-baseline) - 4px);
  width: calc(100% - var(--timeline-baseline) - 14px);
}

/* Pages */

.page-header {
  padding-left: 0;
  padding-right: 0;
}

.projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.project-item {
  text-align: center;
  border-radius: 10px;
  margin: 20px 10px;
}

.project-item:hover {
  box-shadow: 0 0 10px 1px #DADADA;
}

.ant-avatar {
  margin-right: 7px;
  margin-bottom: 4px;
}